:root {
	@include mq() {
		--baseFontSize: 15px;
	}
	@include mq(lg) {
		--baseFontSize: 16px;
	}
}

//600px over
@include mq(md) {
	#header {
		max-width: initial;
	}

	#footer {
		max-width: initial;
	}
}

//1000px over
@include mq(lg) {
	//ヘッダーメニュー
	#btn-header-menu {
		display: none;
		content-visibility: none;
	}

	#header-nav {
		background-color: transparent;
		display: block;
		position: relative;
		height: auto;
		width: auto;
		z-index: auto;
		top: initial;
		animation: none;
		margin-inline-start: 3vw;
		font-weight: 400;
		font-size: $smallFontSize;
		letter-spacing: 0;

		.bl_menu {
			max-width: $contentWidthMax;
			margin: 0 auto;
			display: flex;
			flex-direction: row;
			flex-basis: 8vw;
			align-items: flex-start;
			justify-content: center;
			gap: 2.2vw;

			> li {
				position: relative;
				text-align: center;
			}
		}

		.sub-menu {
			transition: 0.3s;
			transform: translateY(-100%);
			opacity: 0;
			position: absolute;

			margin-left: 0;
			width: 100%;
			background-color: var(--backgroundColor);
		}
		.main-menu-item:hover > .sub-menu,
		.main-menu-item:active > .sub-menu,
		.main-menu-item:focus > .sub-menu {
			opacity: 1;
			transform: translateY(0);
			padding: 1rem;
		}
	}

	//サイドバー
}
