.faq {
	margin: 0;
	padding: 0;

	li {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	dl {
		background-color: $backgroundColor;
		//枠
		border-radius: $radius;
		padding: 1rem;
	}

	dt {
		align-items: center;
		//Q
		display: flex;
		flex-direction: row;
		//font-size:;
		font-weight: bold;
		gap: 1rem;
	}

	dd {
		background-color: lighten($thirdcolor, 2%);
		border-radius: $radius;
		//A
		padding: 1rem 3.7rem;
	}
}

.faq dt:before {
	align-content: center;
	align-items: center;
	color: $linkcolor;
	content: '?';
	display: flex;
	font-size: 2rem;
	font-weight: bold;
	@include mincho;
	justify-content: center;
	min-height: var(--size);
	min-width: var(--size);
	--size: 2.5rem;
}

//開閉
.faq {
	--time: 0.4s;

	dt {
		&:hover {
			color: $secondary;
		}

		&::after {
			background-color: lighten($primaryTextColor, 50);
			content: '';
			display: block;
			height: var(--size);
			margin-inline-end: 0;
			margin-inline-start: auto;
			mask-image: url(./images/icon/angle-down-solid.svg);
			mask-position: center;
			mask-repeat: no-repeat;
			--size: clamp(12px, 0.5rem + 2vw, 24px);
			transition: var(--time);
			width: var(--size);
		}
		&.active::after {
			transform: rotate(180deg);
		}
	}

	dd {
		height: 0;
		line-height: 0;
		opacity: 0;
		overflow: hidden;
		padding: 0 3.7rem;
		transition-duration: var(--time);
		&.open {
			display: block;
			height: auto;
			line-height: $articleLineHeight;
			margin-block-start: 1rem;
			opacity: 1;
			padding: 1rem 3.7rem;
		}
	}
}
