//メニューボタン
#btn-header-menu {
	width: $sqBtnSize;
	height: $sqBtnSize;
	border-radius: $radius;
	position: fixed;
	top: 0;
	right: 0;
	z-index: 150;

	&:hover {
		background-color: rgba($primaryTextColorRgb, 0.1);
	}

	.rect,
	.part {
		fill: none;
	}
	.part {
		stroke: var(--primaryTextColor);
		stroke-width: 2px;
		stroke-linecap: round;
		stroke-linejoin: round;
		transition: 0.2s;
		transform-origin: center;
	}

	&[aria-pressed='true'] {
		#icon {
			.line01 {
				transform: translateY(10px) translateX(-8px) rotate(45deg);
			}
			.line02 {
				opacity: 0;
			}
			.line03 {
				transform: translateY(-7px) translateX(-8px) rotate(-45deg);
			}
		}
	}
	&[aria-pressed='false'] {
		#icon {
			.line01 {
				transform: none;
			}
			.line02 {
				opacity: 1;
			}
			.line03 {
				transform: none;
			}
		}
	}
}

#header-nav {
	background-color: rgba($backgroundColorRgb, 0.9);
	opacity: 0;
	height: 0;
	position: fixed;
	z-index: 100;
	right: 0;
	top: 0;
	max-width: 100%;
	width: 0;
	transition: 0.3s;

	a {
		color: $primaryTextColor;
	}

	&.is-open {
		opacity: 1;
		display: block;
		overflow-y: auto;
		width: max(300px, 34vw);
		height: 100vw;
	}

	.sub-menu {
		margin-left: 1rem;
	}
}
