//.page-template-lp_template

blockquote {
	color: rgba($primaryTextColorRgb, 0.9);
	background-color: $quoteBackgroundColor;
	padding: 1rem;
}

.el_postTitle {
}

.el_snsIcon {
	width: var(--snsBtnSize);
	height: var(--snsBtnSize);
}

.el_card {
	padding: min(1rem, 5vw);
	border-radius: $radius;
}

//CTAボタン
.el_btn {
	color: $backgroundColor; //文字色
	background-color: $linkcolor; //ベースカラー
	border-radius: 6.5vw;
	outline: min(1vw, 10px) solid $backgroundColor; //枠線カラー

	font-size: min(5vw, 24px);
	font-weight: 700;
	text-align: center;
	line-height: 1.2;
	text-decoration: none;
	letter-spacing: 0;

	display: block;
	margin: 0.5em auto;
	padding: min(4vw, 1.5rem) min(6vw, 3rem);
	transition: 0.5s;

	&:hover {
		filter: opacity(1);
		transform: scale(1.05);
	}
}

article {
	p:not(:last-child),
	ul:not(:last-child),
	ol:not(:last-child) {
		margin-block-end: 1rem;
	}
	li {
		margin-left: $wrapMargin;
	}
}

.sidebar {
	li {
		list-style-type: none;
	}
}

//フォームボタン
.wp-block-search__button {
	background: #f7f7f7;
	border: 1px solid #ccc;
	padding: 0.375em 0.625em;
	color: #32373c;
	margin-left: 0.625em;
	word-break: normal;
	font-size: inherit;
	font-family: inherit;
	line-height: inherit;
	width: 15%;
	min-width: 5rem;
}
