#header {
	background-color: rgba($backgroundColorRgb, 0.8);

	> .description {
		display: none;
	}
}

.header-title {
	font-size: $titleFontSize;
	font-weight: bold;
}

.header-title-link {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: center;
	justify-content: flex-start;
	gap: 1rem;

	color: $primaryTextColor;
}
