.el_btnGoTop {
	--margin: min(2vw, 10px);

	@keyframes fadeIn {
		from {
			bottom: -110%;
			opacity: 0;
		}
		to {
			bottom: var(--margin);
			opacity: 1;
		}
	}
	@keyframes fadeOut {
		from {
			bottom: var(--margin);
			opacity: 1;
		}
		to {
			bottom: -110%;
			opacity: 0;
		}
	}

	background-color: $primaryTextColor;
	@include mask('./images/icon/angles-up-solid.svg');

	width: $sqBtnSize;
	height: $sqBtnSize;
	position: fixed;
	right: var(--margin);
	bottom: var(--margin);
	z-index: 50;
	filter: opacity(0.5);
	transition: 0.5s;

	&:hover {
		filter: opacity(1);
	}

	&.st_btnFadeIn {
		animation: fadeIn 0.5s forwards;
	}
	&.st_btnFadeOut {
		animation: fadeOut 0.5s forwards;
	}

	.gotop_svg {
		color: $primaryTextColor;
	}
}
