@mixin base {
	padding-right: max(1rem, calc(50% - #{$contentWidth} / 2));
	padding-left: max(1rem, calc(50% - #{$contentWidth} / 2));
}

@mixin big {
	padding-right: max(1rem, calc(50% - #{$contentWidthMax} / 2));
	padding-left: max(1rem, calc(50% - #{$contentWidthMax} / 2));
}

@mixin small {
	padding-right: max(1rem, calc(50% - #{$contentWidthSmall} / 2));
	padding-left: max(1rem, calc(50% - #{$contentWidthSmall} / 2));
}

.lp {
	article {
		display: flex;
		flex-direction: column;
		flex-wrap: nowrap;
		align-items: center;
		justify-content: flex-start;

		section,
		.firstView {
			width: 100%;
		}
	}
	.content {
		margin: 0;
		padding: $contentpad 1rem;
		overflow: hidden; //子要素のmarginの影響をなくすため
		position: relative;
	}
	.second,
	.flow {
		background-color: $secondary;
		@include base();
	}

	.third {
		background-color: gray;
		@include big();
	}

	.company,
	.faq {
		background-color: $thirdcolor;
		@include small();
	}
}
