//メディアクエリ
$breakpoints: (
	'sm': 'screen and (max-width: 509px)',
	'md': 'screen and (min-width: 600px)',
	'lg': 'screen and (min-width: 1000px)',
) !default;
@mixin mq($breakpoint: md) {
	@media #{map-get($breakpoints, $breakpoint)} {
		@content;
	}
}

//文字サイズ
:root {
	--baseFontSize: 14px;
}

$baseFontSize: var(--baseFontSize);
$articleLineHeight: 1.9; //記事内行間
$smallFontSize: 0.875rem;
$titleFontSize: 2rem;

//レイアウト
$contentWidthAll: 100vw; //最大幅
$contentWidthMax: 1200px; //コンテンツ最大幅
$contentWidth: 900px; //コンテンツの横幅
$contentWidthSmall: 640px;
$contentWidthMin: 300px; //最小幅
$wrapMargin: 1.5rem; //全体の余白
$mainMargin: 1.5rem; //コンテンツエリアの上下余白
$lyGap: clamp(10px, 0.5rem + 2vw, 2rem); //レイアウト要素内のgap余白
$contentpad: min(15vw, 70px);
$radius: 10px; //角丸の有無
:root {
	--lyGap: 2rem; //レイアウト要素内のgap余白
}

@mixin widthMax {
	//幅いっぱい
	margin: 0 calc(50% - 50vw);
	padding: 0 $lyGap;
}

//前後に同じもの配置 ＼(-o-)／
@mixin beforeafter($color, $width: 2px, $height: 28px, $top: 8px) {
	content: '';
	display: inline-block;
	position: relative;
	top: $top;
	width: $width;
	height: $height;
	background-color: $color;
	//    transform: rotate(-25deg);などを追加して使用
}

//iconなどmask
@mixin mask($path) {
	mask-image: url($path);
	mask-position: center;
	mask-repeat: no-repeat;
}

$sqBtnSize: clamp(32px, 1rem + 4vw, 48px); //ハンバーガーメニューのサイズ
:root {
	--snsBtnSize: 2rem; //SNSボタンのサイズ
}

//色指定
:root {
	--primaryTextColor: #444;
	--primaryTextColorRgb: 68, 68, 68;
	--secondary: #0fa7c2;
	--third: #e2e9eb;
	--accent: #e66944;
	--backgroundColor: #fff;
	--backgroundColorRgb: 255, 255, 255;
	--quoteBackgroundColor: #eaeaea;
	--linkcolor: rgb(15, 167, 194);
	--firstViewH2Color: #fff;
}
$primaryTextColor: #444;
$primaryTextColorRgb: var(--primaryTextColorRgb);
$backgroundColor: var(--backgroundColor);
$backgroundColorRgb: var(--backgroundColorRgb);
$quoteBackgroundColor: var(--quoteBackgroundColor);
$linkcolor: var(--linkcolor);
$secondary: rgb(15, 167, 194);
$thirdcolor: rgb(236, 239, 240);
$accentcolor: #e66944;

//フォント指定
@mixin gothic {
	font-family: -apple-system, BlinkMacSystemFont, Roboto, Verdana,
		'游ゴシック Medium', '游ゴシック体', 'Yu Gothic Medium', YuGothic,
		'ヒラギノ角ゴ ProN', 'Hiragino Kaku Gothic ProN', sans-serif;
}

@mixin mincho {
	font-family: 'Noto Serif JP', YuMincho, 'Yu Mincho', serif;
}
