.content.flow {
	--padding: 8vw;

	padding-right: var(--padding);
	padding-left: var(--padding);
}

.flow_list {
	border-radius: $radius;

	.el_card {
		--iconsize: clamp(80px, 1rem + 14vw, 150px); //アイコンの大きさ
		position: relative;

		width: 100%;
		max-width: 600px;

		display: flex;
		flex-wrap: nowrap;
		flex-direction: row;
		align-items: center;
		gap: 5vw;

		&:first-child {
			margin-block-start: $lyGap;
		}

		&:last-child {
			margin-block-end: $lyGap;
		}

		&::before {
			//アイコン画像
			display: block;
			content: '';
			background-color: $backgroundColor;
			border-radius: 50%;
			aspect-ratio: 1;

			height: calc(0.9 * var(--iconsize));
			width: calc(0.9 * var(--iconsize));
		}
	}
	.flow_part {
		//内容
		background-color: $backgroundColor;
		border-radius: $radius;
		padding: 1rem;
		min-height: 150px;
		max-width: 500px;
	}
	h3 {
		font-size: 1.5rem;
		color: $accentcolor;
		margin: 0 0 1.5rem;
		text-align: left;
	}
	p {
		margin: 0;
	}
}

@include mq(lg) {
	.flow_list {
		.el_card {
			margin: 0;
			width: 250px;

			flex-direction: column;
			gap: 1.5rem;

			&:first-child {
				margin-block-start: 0;
			}

			&:last-child {
				margin-block-end: 0;
			}

			&::before {
			}
		}
	}
}
