.bl_columnGrid {
	--min: 15ch;
	--gap: 1rem;

	display: grid;
	gap: var(--gap);
	grid-template-columns: repeat(auto-fit, minmax(min(100%, var(--min)), 1fr));
}

.br_ie .bl_columnGrid {
	grid-template-columns: repeat(5, 1fr);
	grid-template-rows: repeat(1, 1fr);
	> * {
		justify-self: center;
	}
}

.bl_contentWrapper {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.bl_cardWrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: nowrap;
	flex-direction: column;

	gap: clamp(1.5rem, 0.5rem + 2vw, 3rem);
}
@include mq(lg) {
	.bl_cardWrapper {
		flex-wrap: wrap; //PCサイズで横並び折り返しあり
		flex-direction: row;
	}
}
