//ヘッダー画像はcustomizer.phpで管理

.firstView {
	.copy {
		--shadow: rgba(0, 0, 0, 0.3);
		color: var(--firstViewH2Color);
		text-shadow: 1px 1px 0 var(--shadow), -1px -1px 0 var(--shadow),
			-1px 1px 0 var(--shadow), 1px -1px 0 var(--shadow),
			0px 1px 0 var(--shadow), 0-1px 0 var(--shadow), -1px 0 0 var(--shadow),
			1px 0 0 var(--shadow);
		text-align: center;
		letter-spacing: -0.15em;
		font-weight: 800;
		font-size: 3rem;
		margin: 0;
	}
}

.entry {
	width: max(60vw, 300px);
	margin: 0 auto;
	text-align: center;
	position: absolute;
	right: 50%;
	bottom: 0;
	transform: translate(50%, 35%);
}
.entry-desc {
	--col: #fff;
	color: var(--col);
	font-weight: 700;
	font-size: 1.2rem;
	&::before,
	&::after {
		@include beforeafter(var(--col));
	}
	&::before {
		transform: rotate(-25deg);
		margin-right: 1rem;
	}
	&::after {
		transform: rotate(25deg);
		margin-left: 1rem;
	}
}
