.page-template-lp_template {
	//ベース
	h2,
	h3,
	h4 {
		text-align: center;
	}
	li {
		list-style: none;
		margin: 0;
		padding: 0;
	}
	//見出し
	h2.h01 {
		margin-block-end: $contentpad;
	}

	//CTAボタン
	.el_btn {
		color: $backgroundColor; //文字色
		background-color: $linkcolor; //ベースカラー
		border-radius: 6.5vw;
		outline: min(1vw, 10px) solid $backgroundColor; //枠線カラー

		font-size: min(5vw, 24px);
		font-weight: 700;
		text-align: center;
		line-height: 1.2;
		text-decoration: none;
		letter-spacing: 0;

		display: block;
		margin: 0.5em auto;
		padding: min(4vw, 1.5rem) min(6vw, 3rem);
		transition: 0.5s;

		&:hover {
			filter: opacity(1);
			transform: scale(1.05);
		}
	}
}
