html {
	color: $primaryTextColor;
	font-size: $baseFontSize;
}

body {
	overflow-x: hidden;
	@include gothic;
}

#wrapper {
	position: relative;
}

#header,
#main {
	position: relative;
	margin: 0 auto;
}

#header {
	@include widthMax;
	max-height: 75px;
	height: max(6vw, 54px);
	padding: 0.5rem;
	position: sticky;
	top: 0;
	z-index: 100;

	display: flex;
	flex-direction: row;
	align-content: center;
	align-items: center;
	justify-content: center;
}

#main {
}

.firstView {
	position: relative;
	height: 155vw;
	max-height: 600px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	@include widthMax;
	margin-bottom: min(12vw, 80px);
	padding: 0 3rem;
}

.entry {
	display: flex;
	flex-direction: column;
	align-items: center;
}

#footer {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: var(--lyGap);
	padding: 0;
	padding-top: min(5vw, 2.5rem);
	padding-bottom: min(6vw, 3rem);
	.bl_nav,
	.bl_snsWrapper {
		width: 50vw;
		max-width: $contentWidth;
		min-width: $contentWidthMin;
	}
}
.br_safari #footer {
	> *:not(:first-child) {
		margin-top: var(--lyGap);
	}
}

//Googlemap
iframe {
	width: 100%;
	height: clamp(200px, 30vw, 300px);
}

//サイドバー
.sidebar_on {
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: flex-start;

	article {
		width: 100%;
	}

	.sidebar {
		width: 100%;
	}

	.sidebar-widget {
		margin-block-end: 1rem;
	}

	@include mq(lg) {
		flex-direction: row;

		article {
			width: 64%;
			min-width: 600px;
			max-width: 100%;
		}

		.sidebar {
			width: 33%;
			max-width: 340px;
		}
	}
}
